.topbar {
  width: 100%;
  height: 50px;
  background-color: rgb(145, 135, 135);
  position: sticky;
  top: 0;
  z-index: 999;
}
.topbarWrapper {
  height: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo {
  font-size: 35px;
  font-weight: bold;
  color: rgb(0, 0, 0);
}
.baricon{
  margin-right: 5px;
  font-size: 20px !important;
}
.logoname{
  font-weight: bold; 
  margin-left: 10px;
}
.topRight{
  font-weight: bold; 
  margin-left: 10px;
  font-size: 20px;
}
